.newpropeties-block{
	/*position: fixed;*/
}
.newpropeties-header {
	position:relative;
	left:130px;
	top:120px;
	width: calc(100vw - 260px);
}

.newproperties-title {
	width: 254px;
	height: 48px;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}

.newproperties-description{
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #828282;
}
.search-field{
	position: absolute;
    top: 3px;
    right: 0px;
}

.newproperty-search-group {
    position: absolute;
    right: 0px;
}
.newproperty-search {
	position: relative;
	width: 325px;
	border: 0;
	outline: 0;
	background: transparent;
	border-bottom: 1px solid #DDDDDD;
}
.newproperty-search-placeholder {
	position: relative;
	top:-25px;
	transition: all 200ms;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #CBCACA;
	user-select: none; 
}
.newproperty-search-placeholder:hover {
	  cursor: text;
}

.newproperty-search:focus + .newproperty-search-placeholder ,
.newproperty-search:valid + .newproperty-search-placeholder  {
  font-size: 60%;
  transform: translate3d(0, -100%, 0);
}

.search-button {
    position: relative;
}

.newproperty-carousel{
	position: relative;
	margin-left: 130px;
	max-width: 90%;
	margin-top: 500px;
	
}
.card-body {
    padding: 0.25rem	
}
.card {
	width: 250px;
	height: 340px;
	border: none;
	cursor: pointer;
}

.card:hover {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.5);
	transform: scale(1.01);
}
.card:active {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.7);
}
.CB9vby5YRbJWpv7hRrZ0x {
	height: 360px;
}
.card-img-top:hover {
		border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.card-img-top{

	display: block;
	width: 100% !important;
	height: 174px;

}
.card-title {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 21px;
	font-size: 14px;
	text-align: left;
	text-transform: uppercase;

	color: #4F4F4F;
}
.card-price {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 35px;
	font-size: 24px;
	text-transform: uppercase;
	color: #2D9CDB;
	position: absolute;
	bottom: 0px;
	right: 20px;
}

.card-text {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 19px;
	font-size: 12px;
	color: #000000;
}



@media (min-width: 320px) and (max-width: 767px){
	.newproperty-carousel{
		margin: auto;
	}
	.newproperties-text {
    margin-top: 120px;
    margin-left: 20vw;
}
}
@media (min-width: 320px) and (max-width: 767px){
	.newproperty-carousel {
		margin: auto;
	}
	.newproperties-text {
    margin-top: 120px;
    text-align: center;
}
}

@media (min-width: 992px) and (max-width: 1399px){
	.newpropeties-header {
		left:65px;
		width: calc(100vw - 130px);
	}
}

